:root {
    --mainBlue: #2a2a72;
    --lightBlue: #00a2ff8c;
    --mainWhite: #ffffff;
    --mainDark: #232528;
    --mainYellow: #ffa400;
    --secondDark: #3F4A59;
}

body {
    color: var(--secondDark) !important;
    background: var(--mainWhite) !important;
    font-family: "Bryant MOO Pro Medium", "Trebuchet MS", Trebuchet, "Liberation Sans", sans-serif !important;
}

.container {
    color: #f5ba4eda;
